<template>
	<div class="wrapper">
		<Tariff v-for="item in tariffs" 
						v-bind:tarif="item">
			
		</Tariff>

	</div>
</template>

<script>
	import Tariff from './Tariff.vue';

	export default {
		data() {
			return{
				tariffs: [
					{
						price: '00',
						title: 'Free Trail'
					},
					{
						price: 50,
						title: 'Basic'
					},
					{
						price:99,
						title: 'Ultimates'
					}
				]
			}
		},
		
		components: {
			Tariff
		}
	}
</script>


