<template>
	<div>
		<div class="pricing">
			<Pricing></Pricing>
			
		</div>
		<section class="slider-section">
			<Slider></Slider>
			
		</section>
	</div>
</template>

<script>
	import Pricing from './Pricing.vue';
	import Post from '../components/Post.vue';
	import Slider from '../components/Slider.vue';

	
	export default {
		data(){
			return {
				
				}
			},
			components: {
				Pricing,
				Slider
			}
	}
</script>

<style lang="scss">
$container-max-widths: (sm: 540px, md: 720px, lg: 960px, xl: 1200px);
 @import "../assets/scss/bootstrap-4/scss/bootstrap-reboot.scss";
 @import "../assets/scss/bootstrap-4/scss/bootstrap-grid.scss";
@import "../assets/scss/bootstrap-4/scss/utilities/_sizing.scss";
@import "../assets/scss/main.scss";

	body{
		min-width: 320px;
		overflow-x: hidden;
		font-family: 'Open Sans', sans-serif;
	}

	.pricing{
		padding: 125px 0 125px 0;
		background-color: #f0f1f1;
	}
	.posts-wrapper{
		display: -webkit-flex;
		display: -moz-flex;
		display: -ms-flex;
		display: -o-flex;
		display: flex;
		flex-wrap: wrap;
	}
	.post{
		width: calc(33.3333% - 30px);
		padding: 15px;
		margin-left: 15px;
		margin-right: 15px;
		background-color: pink;
	}
</style>


