<template>
	<div class="tariff">

		<div class="tariff-top">
			<div class="tariff-title">{{tarif.title}}</div>
			<div class="tariff-price"><span>$</span><span class="tariff-price__val">{{tarif.price}}</span><span>per month</span>
			</div>
		</div>
		
		<div class="tariff-body">
			<ul>
				<li v-for="option in options">{{option}}</li>
			</ul>
		</div>
			
		<div class="tariff-bottom">
			<a href="#">Order Now</a>
			
		</div>
		
	</div>

</template>

<script>
	export default {
		props: ['tarif'],
		data(){
			return {
				options: ['30 Free Trail', '5 Free Projects', 'PHP 5 Enabled', '24/7 Suports']
			}
		}
	}
</script>

<style lang="scss" scoped>
	@import '../assets/scss/tariff.scss';
</style>


