<template>
	<div class="container">
		<div class="row justify-content-center">
			<div class="col-lg-10 slider-section__wrapper">
				<v-slider v-bind="options">
					<div v-for="n in 3" class="slider-item">
						<div class="slider-item__img">
							<img src="static/img/slider-img.jpg" alt="alt">
						</div>

						<div class="slider-item__text">
							<div class="slider-item__name">Limara tusi</div>
							<div class="slider-item__role">CEO, RanDOM COMPANY</div>
							<p class="slider-item__review">Lorem ipsum dolor sit amet, consectetuer adipiscing elit. Aenean commodo ligula eget dolor. Aenean massa. Cum sociis natoque penatibus et magnis dis parturient montes, nascetur ridiculus mus. Donec quam felis, ultricies nec,</p>
						</div>
					</div>
				</v-slider>
			</div>
			
		</div>
	</div>
</template>

<script>
	import slider from '../assets/vue-slider-master/src/slider.vue'
	export default {
		// el: 'main',
		data(){
			return {

				options: {
					items: 1,
					margin: 20,
					nav: null,
					dots: true,
					loop: true,
					timing: 'cubic-bezier(0, 0.72, 0.64, 1.06)',
					offset: 1,
					prevNav: 'Туда',
					nextNav: 'Сюда',
					sibling: true,
					responsive : {}
				}
			}
		},
		components: {
			'v-slider': slider
		}
	}
</script>

<style lang="scss">
	@import '../assets/scss/_mixins.scss';
	@import '../assets/scss/slider.scss';


</style>


